<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-Faq')">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')" class="current">자주묻는질문</li>
			<li @click="goto('/admin/terms')">약관</li>
		</ul>

		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop check">
							<input
								type="checkbox"
								class="checkbox"
								name="member"
								id="select-all"
								v-model="selectAll"
							/>
							<label for="select-all"></label>
						</div>
						<div class="prop">카테고리</div>
						<div class="prop" style="width: 220px">문의내용</div>
						<div class="prop" style="width: 385px">답변내용</div>
						<div class="prop ">최종수정일자</div>
						<div class="prop">수정</div>
					</li>
					<slot v-if="!faqList">
						<li class="row no-data body3" style="display: block;">
							등록된 글이 없습니다
						</li>
					</slot>
					<slot v-else>
						<li v-for="item in faqList" :key="item.id" class="row">
							<div class="prop check">
								<input
									type="checkbox"
									class="checkbox"
									name="member"
									:id="item.id"
									:value="item.id"
									v-model="selected"
								/>
								<label :for="item.id"></label>
							</div>
							<div class="prop">{{ item.category }}</div>
							<div class="prop txt-left" style="width: 220px">
								{{ item.title }}
							</div>
							<div class="prop txt-left" style="width: 385px">
								{{ item.contents }}
							</div>
							<div class="prop">
								{{ $moment(item.updateDate).format('YYYY.MM.DD') }}
							</div>
							<div class="prop">
								<router-link
									:to="{ name: 'EditFaqContent', params: { id: item.id } }"
								>
									<Edit />
								</router-link>
							</div>
						</li>
					</slot>
				</ul>
				<div class="bottom-container">
					<Pagination
						v-if="totalPage !== -1"
						:listRowCt="15"
						:pageLinkCt="5"
						:totalCount="totalPage"
						:url="`/admin/rolling-Faq`"
					/>
					<div class="btn-container">
						<div class="checked-info body3 marR-30">
							<span class="number">0</span><span>개 선택됨</span>
						</div>
						<button class="solid-btn red">선택 글 삭제</button>
						<router-link :to="{ name: 'AddFaq' }">
							<button class="solid-btn green">
								새 글 등록
							</button>
						</router-link>
					</div>
					<LoadingSpinner :loading="isLoading" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { fetchAdminFaq, deleteFaq, fetchFaqTotalCount } from '@/api/faq';
import Pagination from '@/components/common/Pagination';
import Edit from '@/assets/icon/Edit';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner, Edit, Pagination },
	created() {
		this.currentPage = this.$route.params.page;
		this.fetchAdminFaq(this.currentPage);
		this.fetchFaqTotalCount();
	},
	data() {
		return {
			faqList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
		};
	},
	computed: {
		selectAll: {
			get: function() {
				return this.faqList
					? this.selected.length == this.faqList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.faqList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchAdminFaq(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchAdminFaq(page - 1);
				this.faqList = data.map(item => {
					if (item.categoryId == 'AC') {
						item.category = '계정';
					} else if (item.categoryId == 'PY') {
						item.category = '결제';
					} else if (item.categoryId == 'SH') {
						item.category = '배송';
					} else if (item.categoryId == 'PD') {
						item.category = '프로그램 및 굿즈';
					} else if (item.categoryId == 'HP') {
						item.category = '홈페이지 이용';
					} else if (item.categoryId == 'ETC') {
						item.category = '기타';
					}
					return item;
				});
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
		async fetchFaqTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchFaqTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
		async deleteFaq() {
			this.isLoading = true;
			try {
				await deleteFaq(this.selected);
				this.fetchAdminFaq(this.currentPage);
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchAdminFaq(this.currentPage);
			}
		},
	},
};
</script>

